import React from 'react';
import './login.css';
import {Link, Navigate, Redirect, Route, Routes, useNavigate} from 'react-router-dom';
import VideoGenerator from "../video_generator/VideoGenerator";
import Register from '../register/register';
import '../constants';
import {FACE_ANIMATION_BASE_URL} from "../constants";
class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {setToken: this.props.setToken, loggedIn: false}
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        let formData = new FormData();
        formData.append('username', event.target.username.value);
        formData.append('password', event.target.password.value);
        console.log(JSON.stringify(formData));

        const myInit = { method: 'POST',
            mode: 'cors',
            body: formData
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/login', myInit);
        fetch(request)
            .then(response => response.json())
            .then(data =>{
                console.log(data);
                this.state.setToken(data);
                this.setState({'loggedIn': true});
            });
        event.preventDefault();
    }

    render()
    {
        let loggedIn = this.state.loggedIn;
        if (loggedIn) {
            return (<Navigate to="/" replace={true} />)
        }
        return (
            <div className = "background-container">
                <div className="form-wrapper">
                    <img  className = "login-logo" src={require('./logo.png')}/>
                    <form onSubmit={this.handleSubmit} className="login-form">
                        <div className = "login-row">
                            <svg className="login-icon" viewBox="0 0 20 20">
                                <path d="M0,20 a10,8 0 0,1 20,0z M10,0 a4,4 0 0,1 0,8 a4,4 0 0,1 0,-8"/>
                            </svg>
                            <input type="text" name = "username" className = "login-input"/>
                        </div>
                        <div className = "login-row">
                            <svg className="login-icon" viewBox="0 0 20 20">
                                <path d="M0,20 20,20 20,8 0,8z M10,13 10,16z M4,8 a6,8 0 0,1 12,0"/>
                            </svg>
                            <input type="password" name = "password" className = "login-input"/>
                        </div>
                        <div>
                            <button type="submit" className = "login-button">Login</button>
                        </div>
                        <div className="login-row" id = "login-row-register">
                            <p className = "login-p">Don't have an account? </p>
                            <Link className = "login-register" to='/register'>Sign up</Link>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Login;