import React from 'react';
import {getToken} from "../App";
import './profile.css';
import {FACE_ANIMATION_BASE_URL} from "../constants";

class Profile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {name:'', surname:'', videoNames:[], videos:{}, currentVideo: null}
        this.handleClickVideo = this.handleClickVideo.bind(this);
    }

    componentDidMount() {
        const myInit = { method: 'GET',
            headers: {'Authorization': 'Bearer ' + getToken()},
            mode: 'cors'
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/profile', myInit);
        fetch(request)
            .then(response => response.json())
            .then(data =>{
                console.log(data);
                let d = {};
                for (var i of data.videos) {
                    d[i] = '';
                }
                console.log(d);
                this.setState({name:data.name, surname:data.surname, videos:d, videoNames: data.videos});
            });
    }

    handleClickVideo(name) {
        console.log(name);
        const myInit = { method: 'GET',
            headers: {'Authorization': 'Bearer ' + getToken()},
            mode: 'cors'
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/video/' + name, myInit);
        fetch(request)
            .then(response => response.text())
            .then(data =>{
                console.log(data);
                this.setState({
                    ...this.state,
                    videos: {
                        ...this.state.videos,
                        [name]: 'data:video/mp4;base64,' + data
                    }
                });
            });
    }

    render() {
        /*let video = null;
        if (this.state.currentVideo) {
            video = <video crossOrigin="anonymous" src={'data:video/mp4;base64,' + this.state.currentVideo} autoPlay controls></video>
        }*/
        return (
            <div className='background-container' id = 'profile-container'>
                <h1 id = "profile-name">Hi {this.state.name + ' ' + this.state.surname}!</h1>
                <div className='form-wrapper' id = 'profile-wrapper'>
                    {this.state.videoNames.map((option) => (
                        <div className = "profile-video-container">
                        <video className='profile-video' crossOrigin="anonymous" src={this.state.videos[option]} autoPlay controls onClick={e => this.handleClickVideo(option)}></video>
                        <p className='profile-p'>{option}</p>
                        </div>
                    ))}
                </div>
            </div>
        );
    }
}

export default Profile;