import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App, {setToken} from './App';
import reportWebVitals from './reportWebVitals';
import {BrowserRouter, HashRouter, Route, Routes, useNavigate} from "react-router-dom";
import VideoGenerator from "./video_generator/VideoGenerator";
import Register from './register/register';
import Login from "./login/login";
import Profile from "./profile/profile";
const root = ReactDOM.createRoot(document.getElementById('root'));

root.render(
  <React.StrictMode>
      <HashRouter>
      <Routes>
          <Route path="/facial-animation" element={<App/>}/>
          <Route path="/facial-animation/login" element={<Login setToken = {setToken}/>}/>
          <Route path="/facial-animation/register" element={<Register/>}/>
          <Route path="/facial-animation/generate" element={<VideoGenerator/>}/>
          <Route path="/facial-animation/profile" element={<Profile/>}/>
      </Routes>
      </HashRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
