import React from 'react'
import './Displayer.css'
class Displayer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {image: this.props.image, video: this.props.video}
    }

    render() {
        return (
            <div className='form-wrapper' id = "displayer-container">
                    {console.log("rendering displayer" + this.state.image)}
                    <img src = {this.state.image} id = 'uploaded-image'/>
                    <video id = 'received-video' crossorigin="anonymous" src={this.state.video} autoplay controls></video>
            </div>
        )
    }
}
export default Displayer;