import React from "react";
import {useNavigate} from "react-router-dom";
import './register.css';
import {FACE_ANIMATION_BASE_URL} from "../constants";
class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {setToken: this.props.setToken}
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        const formData = new FormData(event.target);
        const data = JSON.stringify(Object.fromEntries(formData));

        const myInit = { method: 'POST',
            mode: 'cors',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: data
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/register', myInit);
        fetch(request)
            .then(response => response.json())
            .then(data =>{
                console.log(data);
            });
        event.preventDefault();
        window.open("/");
    }

    render()
    {
        return (
            <div className = "background-container">
                <div className="form-wrapper">
                    <img  className = "register-logo" src={require('./logo.png')}/>
                    <form onSubmit={this.handleSubmit} className="register-form">
                        <label className="register-row">
                            <p className='register-p'>Name:</p>
                            <input type="text" name = "name" className= "register-input"/>
                        </label>
                        <label className="register-row">
                            <p className='register-p'>Surname:</p>
                            <input type="text" name = "surname" className= "register-input"/>
                        </label>
                        <label className="register-row">
                            <p className='register-p'>Username:</p>
                            <input type="text" name = "username" className= "register-input"/>
                        </label>
                        <label className="register-row">
                            <p className='register-p'>Password:</p>
                            <input type="password" name = "password" className= "register-input"/>
                        </label>
                        <div>
                            <button type="submit" className="register-button">Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Register;