import React from 'react';
import './Form.css'
import {getToken} from "../App";
import {FACE_ANIMATION_BASE_URL} from "../constants";
class Form extends React.Component {
    constructor(props) {
        super(props);

        this.state = {expressions:['A', 'B'], currentExpression:'A', file: '', setImage:this.props.setImage, setVideo: this.props.setVideo}

        this.handleFileChange = this.handleFileChange.bind(this);
        this.handleExpressionChange = this.handleExpressionChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

        this.fileInput = React.createRef();
    }

    componentDidMount() {
        console.log("form did mount" + this.state.expressions);
        const headers = new Headers();
        headers.append('Accept', 'application/json');
        headers.append('Authorization', 'Bearer ' + getToken());
        const myInit = { method: 'GET',
            headers: headers,
            mode: 'cors'
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/expressions', myInit);
        fetch(request)
            .then(response => response.json())
            .then(data => this.setState({expressions: data}));
    }

    handleFileChange(event) {
        console.log("handling file change");
        this.state.setImage(URL.createObjectURL(event.target.files[0]));
        this.setState({file: event.target.files[0]});
    }

    handleExpressionChange(event) {
        console.log("changing expressiong to " + event.target.value);
        this.setState({currentExpression: event.target.value});
    }

    handleSubmit(event) {
        let formData = new FormData();
        console.log(event.target.sourceImage.files[0]);
        formData.append('sourceImage', event.target.sourceImage.files[0]);
        formData.append('expression', event.target.expression.value);
        formData.append('givenName', event.target.givenName.value);

        const myInit = { method: 'POST',
            headers: {'Authorization': 'Bearer ' + getToken()},
            mode: 'cors',
            body: formData
        };
        const request = new Request(FACE_ANIMATION_BASE_URL + '/generate', myInit);
        fetch(request)
            .then(response => response.text())
            .then(data =>{
                console.log(data);
                this.state.setVideo(data);
            });
        event.preventDefault();
    }

    render() {
        return (
            <div className='form-wrapper' id = 'upload-form'>
            <form onSubmit={this.handleSubmit}>

                <label>
                    <p className='form-p'>Source image:</p>
                    <input className='form-input' name = 'sourceImage' type = 'file' ref = {this.fileInput} onChange = {this.handleFileChange}/>
                </label>

                <label>
                    <p className='form-p'>Target expression:</p></label>
                <select name = 'expression' >
                    {this.state.expressions.map((option) => (
                        <option value={option}>{option}</option>
                    ))}
                </select>
                <label className="register-row">
                    <p className='register-p'>Video name:</p>
                    <input type="text" name = "givenName" className= "register-input"/>
                </label>
                <input className='form-button' type = 'submit'/>
            </form>
            </div>
        );
    }
}

export default Form;